import React, { Component } from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { setActivePage } from '../../actions/action-set-active-page';
import TinderCard from '../../components/tinder-card'
import DoNotFeed from '../../components/do-not-feed'
import safely from '../../assets/images/safely.png';
import time from '../../assets/images/time.png';
import feather from '../../assets/images/feather.png';
import birdGround from '../../assets/images/bird-ground.png';
import orphaned from '../../assets/images/orphaned.png';
import injured from '../../assets/images/injured.png';
import ProbablyOk from '../../components/probably-ok';
import EatOnGround from '../../components/eat-on-ground';
import LargeBird from '../../components/large-bird';
import SmallBird from '../../components/small-bird';

class Flow extends Component {
  state = {
    flow: [
    {
      id: '0',
      section: 'Identifying Animal',
      title: `Is it a ${this.props.animal.name}?`,
      image: <img alt="Your pic" src={this.props.animal.url} />,
      onRight: 1,
      onLeft: 0,
    },
    {
      id: '1',
      title: `Does the ${this.props.animal.name} appear to be sick or injured?`,
      image: <img alt="Your pic" src={injured} />,
      onRight: 6,
      onLeft: 2,
    },
    {
      id: '2',
      title: `Is the ${this.props.animal.name} fully feathered?`,
      image: <img alt="Your pic" src={feather} />,
      onRight: 3,
      onLeft: 9,
    },
    {
      id: '3',
      title: `Is the ${this.props.animal.name} on or near the ground?`,
      image: <img alt="Your pic" src={birdGround} />,
      onRight: 4,
      onLeft: 5,
    },
    {
      id: '4',
      title: `Has the ${this.props.animal.name} been on the ground for less than 1 hour?`,
      image: <img alt="Your pic" src={time} />,
      onRight: 10,
      onLeft: 9,
    },
    {
      id: '5',
      title: `Do you think the ${this.props.animal.name} is orphaned?`,
      image: <img alt="Your pic" src={orphaned} />,
      onRight: 9,
      onLeft: 11,
    },
    {
      id: '6',
      title: `Is the ${this.props.animal.name} too big to handle safely?`,
      image: <img alt="Your pic" src={safely} />,
      onRight: 7,
      onLeft: 8,
    },
    {
      id: '7',
      section: 'What to do',
      confirmation: true,
      children: <LargeBird />,
    },
    {
      id: '8',
      section: 'What to do',
      confirmation: true,
      children: <SmallBird />,
    },
    {
      id: '9',
      section: 'What to do',
      confirmation: true,
      children: <DoNotFeed />,
    },
    {
      id: '10',
      section: 'What to do',
      confirmation: true,
      children: <EatOnGround />,
    },
    {
      id: '11',
      section: 'What to do',
      confirmation: true,
      children: <ProbablyOk />,
    },
  ],
    currentPage: 0
  }

  changePage = (currentPage, card, direction) => {
    this.setState({
      currentPage: direction === 'left' ? card.onLeft : card.onRight
    })
  }

  render() {
    const {changePage} = this;

    const {
      flow,
      currentPage
    } = this.state;

    return (
      <TinderCard
        confirmation={flow[currentPage].confirmation}
        section={flow[currentPage].section}
        title={flow[currentPage].title}
        image={flow[currentPage].image}
        onLeft={() => changePage(currentPage, flow[currentPage], 'left')}
        onRight={() => changePage(currentPage, flow[currentPage], 'right')}
      >
        {flow[currentPage].children && flow[currentPage].children}
      </TinderCard>
    );
  }
}

function mapStateToProps(state) {
  return {
    animal: state.animal,
    flow: state.flow
  };
}

function matchDispatchToProps(dispatch) {
  return bindActionCreators({
    setActivePage,
  }, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(Flow);
