import React from 'react';
import {Tick, Cancel} from '../icons';
import './tinder-card.scss';

const TinderCard = ({
  section='Assessing Situation',
  title='',
  image= '',
  children= <span/>,
  confirmation = false,
  onRight = () => {},
  onLeft = () => {}
}) => {
  return (
    <div className="c-tinder-card">
      <div className="c-tinder-card__section">
       <h3>{section}</h3>
      </div>
      { !confirmation && 
        <>
          <div className="c-tinder-card__title">
            <h1>{title}</h1>
          </div>
          <div className="c-tinder-card__img">
            {image}
          </div>
          <div className="c-tinder-card__btns">
            <button onClick={onRight}><Tick /></button>
            <button onClick={onLeft}><Cancel /></button>
          </div>
        </>
      }
      { children }
    </div>
  );
}

export default TinderCard;